import Vue from 'vue'
import Router from 'vue-router'
import hom from '@/components/hom'
import music from "../components/music"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'hom',
      component: hom
    },
    {
      path: '/music',
      name: "music",
      component: music
    }
  ]
})



