<template>
  <el-container class="container">
    <el-header class="header">
      <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1" class="el-icon-s-home">首页</el-menu-item>
        <el-menu-item index="2" class="el-icon-s-grid">关于</el-menu-item>
        <el-menu-item index="3" class="el-icon-headset">音乐播放功能</el-menu-item>
      </el-menu>
    </el-header>

    <el-container class="subject">
      <el-aside width="200px" class="aside">
        <music></music>
        </el-aside>
        <el-main>
          <h2 class="welcome">Axzyz博客网站欢迎您!</h2>
          <h3>本站于二零二零年五月一号正式发布</h3>
          <h4>有问题欢迎大家咨询此邮箱！</h4>
          <h2>axyzyz@qq.com</h2>
        </el-main>
    <el-drawer
  title="关于"
  :visible.sync="drawer"
  :direction="direction"
  :before-close="handleClose">
  <span>
    第一条：本站所有歌曲来自互联网抓取，服务器中不保存任何歌曲文件及信息，若本站无意间侵犯了您权益，可联系邮箱
    <h2>axyzyz@qq.com</h2>
    <br>
    第二条：本站刚刚成立，许多功能尚未完善，如有不足之处还请多多包含。如有雷同纯属盗版，请联系作者!
    <br>
    第三条：所有解释权归本站站长所有。
  </span>
</el-drawer>
</el-container>
<el-footer>
  <el-link type="primary" href='#'>友情连接处</el-link>
</el-footer>
  </el-container>
</template>

<script>
import music from "./music"
import axios from "axios";
import marked from "marked";
export default {
  name: "HelloWorld",
  data() {
    return {
      about : false,
      activeIndex: '1',
      drawer: false,
      direction: 'rtl',
      htmlMD:'',
    };
  },
  mounted : function () {
  },
  methods: {
    handleSelect(key, keyPath) {
      if (key==2) {
        this.drawer = true
      } else if (key==3) {
        this.$alert('此功能真正开发中。。。', '抱歉', {
          confirmButtonText: '确定',
          callback: action => {
            this.activeIndex = 1;
          }
        });
      }
    },

      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
            this.activeIndex = 1;
          })
          .catch(_ => {

          });
      },
      
  },
  components: {
    music
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container{
  background: url("https://shop.io.mi-img.com/app/shop/img?id=shop_66a5ff50ace715c30a94357900505301.jpeg") no-repeat 50% 50%;
  background-size: cover;
  position:absolute;
  width: 100%;
  height: 100%;
}
#a{
width: auto;
height: auto;
float: right;
margin-top: 10px;
}
.el-menu-demo{
  background: none;
}
#main{
  padding: 5%;
  height: 60%;
  width: 60%;
}
.aside{
  /* position: absolute; */
  margin: 10px auto;
  height: 80%;
  width: 30%;
  border-right:1px dashed #000
}
.subject{
  width: 100%;
  height:80%;
}

.welcome{
  color: red;
}
</style>



