<template>

  <div id="musicPlayer">
    <div id="musicCover">
      <div id="imageBorder"></div>
      <div id="coverImage"></div>
    </div>
    <div id="musicController">
      <div
        id="musicName"
        style="font-size:0.8em; height:1.4em; overflow: hidden; font-weight:700; user-select:none; -webkit-user-seletct:none; -moz-user-seletct:none; -ms-user-seletct:none;"
      ></div>
      <svg
        id="previousSong"
        class="icon"
        aria-hidden="true"
        style="font-size:1.8em; margin-right:5px; cursor:pointer"
        v-on:click="previousMusic()"
      >
        <use xlink:href="#icon-shangyishou" />
      </svg>
      <svg
        id="play"
        class="icon"
        aria-hidden="true"
        style="font-size:2.5em; margin-right:5px; cursor:pointer"
        v-on:click="playMusic()"
      >
        <use xlink:href="#icon-weibiaoti--" />
      </svg>
      <svg id="nextSong" class="icon" aria-hidden="true" style="font-size:1.8em; cursor:pointer" v-on:click="nextMusic()">
        <use xlink:href="#icon-xiayishou" />
      </svg>
    <svg
        id="playMode"
        class="icon"
        aria-hidden="true"
        style="font-size:1em; cursor:pointer"
        v-on:click="playModeChange()"
      >
        <use xlink:href="#icon-xunhuanbofang" />
      </svg>
      <div
        id="musicBar"
        style="width:90%; height:3px; margin:0 auto; margin-top:3px; margin-bottom:3px; background:rgb(190, 190, 190); cursor:pointer;"
        @mousedown="musicBarChange()"
      >
        <div style="height:3px; background:rgb(100, 100, 100);"></div>
        <div
          style="margin-top:-3px; width:0; height:3px; min-width:0px; max-width:100%; background:rgb(60, 60, 60);"
        ></div>
      </div>

      <svg
        id="lyrics"
        class="icon"
        aria-hidden="true"
        style="font-size:1em; margin-right:5px; cursor:pointer"
      >
        <use xlink:href="#icongeci" />
      </svg>
      <svg
        id="songList"
        class="icon"
        aria-hidden="true"
        style="font-size:1em; margin-right:5px; cursor:pointer"
      >
        <use xlink:href="#iconliebiao1" />
      </svg>

    </div>
    <div id="musicList"></div>
  </div>
</template>
<script>
export default {
  name: "musicPlayer",
  data:function(){
    return{
      music : null,
      coverImage : this.$refs.coverImage,
      imageBorder : this.$refs.imageBorder,
      musicItem : null,
      musicName : null,
      musicBar : null,
      musicBarBuffer : null,
      musicBarValue : null,
      musicRootPath : "https://music.163.com/song/media/outer/url?id=",
      coverRootPath : "https://p1.music.126.net/",
      musicObj : null,
      musicList : null,
      playMode : ["icon-xunhuanbofang","icon-danquxunhuan","icon-icon-"],
      scale : 0,
      musicCoverMove : null,
      musicBarMove : null,
    }
  },
  created:function(){
    this.music = document.createElement("audio");
    this.music.playMode = 0;
    const coverRootPath = this.coverRootPath;
    const musicRootPath = this.musicRootPath;
    this.musicObj = function(name,musicSource,coverSource){
      this.name = name;
      this.cover = coverRootPath + coverSource +"?param=500y500";
      this.src = musicRootPath + musicSource;
    }
    const musicObj = this.musicObj;
    this.musicList = [
        new musicObj('夜空中最亮的星','25706282.mp3','d541jx7SqoDzVkenhmtg6g==/19193074974743620.jpg'),
        new musicObj('我的梦 (Live)','431853688.mp3','2tEEJqcH45znTWs18CBOXQ==/1376588573856953.jpg'),
        new musicObj('蓝莲花(Live)','167908.mp3','OvgbsC9XCCtKXtp7GB_Xyg==/91259465106346.jpg'),
        new musicObj('和你一样','254359.mp3','4xv2Jsuhe1DBkDfIeVlaYQ==/39582418616705.jpg'),
        new musicObj('In The End (Mellen Gi Remix)','1316375729.mp3','oHYj5XGA1NqVgr4KV24wsA==/109951163597648329.jpg'),
        new musicObj('9420','515143305.mp3','tyOXJ8U9TisNRfcorQ-4og==/109951163050074297.jpg'),
        new musicObj('City of sky','864711417.mp3','d4Km-iVxvkjKeaeV7U-51w==/109951163394461179.jpg'),
        new musicObj('真的爱你','346075.mp3','ghmbmEQS-IJfZPjdA3KGxg==/82463372084291.jpg'),
        new musicObj('慢慢喜欢你','541687281.mp3','21l0Cy8SO6ycY_eZT-6vVw==/109951163386949943.jpg'),
        new musicObj('云烟成雨','513360721.mp3','DSTg1dR7yKsyGq4IK3NL8A==/109951163046050093.jpg'),
        new musicObj('可能否','571074119.mp3','dclGmiJBDktn9aqafE-JiA==/109951163333343385.jpg'),
        new musicObj('最美的太阳','191278.mp3','5KnV1DJyAtQEAwlhCANNQw==/109951163077748578.jpg'),
        new musicObj('蛮荒','557459667.mp3','42Oe0hddzssDenCGnMautw==/109951163281015978.jpg'),
        new musicObj('POP/STARS','1321385655.mp3','CaFZORZOOdE3acaCqMxn2Q==/109951163861068723.jpg'),
      ];
    this.musicItem = this.musicList[0];
    this.music.setAttribute("src", this.musicItem.src);
  },
  mounted:function(){
    this.coverImage = document.querySelector("#coverImage");
    this.imageBorder = document.querySelector("#imageBorder");
    this.musicName = document.querySelector("#musicName");
    this.musicBar = document.querySelector("#musicBar");
    this.musicBarBuffer = this.musicBar.childNodes[0];
    this.musicBarValue = this.musicBar.childNodes[2];

    const musicList = this.musicList;
    for (let i = 0; i < musicList.length; i++) {
      if (i == 0) {
        musicList[i].previous=musicList[musicList.length - 1];
        musicList[i].next=musicList[i + 1];
        continue;
      } else if (i == musicList.length - 1) {
        musicList[i].previous=musicList[i - 1];
        musicList[i].next=musicList[0];
        continue;
      }
      musicList[i].previous=musicList[i - 1];
      musicList[i].next=musicList[i + 1];
    }

    let musicCoverWhirl = ()=>{
      this.musicCoverMove = setInterval(() => {
        this.coverImage.style.webkitTransform =
          "rotate(" + (this.scale += 0.1).toString() + "deg)";
        this.imageBorder.style.webkitTransform =
          "rotate(" + (this.scale += 0.1).toString() + "deg)";
      }, 10)
    }
    let musicBarValueBuff = ()=>{
      this.musicBarMove = setInterval(()=>{
        try{
          this.musicBarBuffer.style.width = this.music.buffered.end(this.music.buffered.length-1) / this.music.duration * 100 + "%";
          this.musicBarValue.style.width = this.music.currentTime / this.music.duration * 100 + "%";
        }catch(err){
          console.log(err);
        }
      },300)
    }

    this.musicName.innerText = this.musicItem.name;

    const music = this.music;
    const musicName = this.musicName;
    const coverImage = this.coverImage;

    music.addEventListener("play",()=>{
      musicCoverWhirl();
      musicBarValueBuff();
      document.querySelector("#play").firstChild.setAttribute("xlink:href", "#icon-weibiaoti--1");
    });

    music.addEventListener("pause",()=>{
      clearInterval(this.musicCoverMove);
      clearInterval(this.musicBarMove);
      document.querySelector("#play").firstChild.setAttribute("xlink:href", "#icon-weibiaoti--");
    });

    const musicCoverStop = this.musicCoverStop;
    music.addEventListener("ended",()=>{
      if(music.playMode==0){
        musicCoverStop();
        clearInterval(this.musicBarMove);
        this.musicItem = this.musicItem.next;
        musicName.innerText = this.musicItem.name;
        coverImage.style.backgroundImage = "url(\"" + this.musicItem.cover +"\")";
        music.setAttribute("src",this.musicItem.src);
        music.play();
      }else if(music.playMode==1){
        music.pause();
        music.setAttribute("src",this.musicItem.src);
        music.play();
      }else if(music.playMode==2){
        musicCoverStop();
        clearInterval(this.musicBarMove);
        this.musicItem = musicList[parseInt((Math.random()*(musicList.length-1))+1)];
        musicName.innerText = this.musicItem.name;
        coverImage.style.backgroundImage = "url(\"" + this.musicItem.cover +"\")";
        music.setAttribute("src",this.musicItem.src);
        music.play();
      }
    });

    for (let i = 0; i < musicList.length; i++) {
    let musicListItem = document.createElement("div");
    musicListItem.innerText = i+1 + ". " + musicList[i].name;
    musicListItem.addEventListener("click", ()=> {
      if (music.getAttribute("src") != musicList[i].src) {
        this.musicItem=musicList[i];
        music.setAttribute("src",this.musicItem.src);
        musicCoverStop();
        clearInterval(this.musicBarMove);
        musicName.innerText = this.musicItem.name;
        coverImage.style.backgroundImage = "url(\"" + this.musicItem.cover +"\")";
        music.play();
      } else if (music.getAttribute("src") == musicList[i].src) {
        if (music.paused == true) {
          music.play();
        } else {
          music.pause();
        }
      }
    });
    document.querySelector("#musicList").appendChild(musicListItem);
  }
  },
  methods:{
    musicCoverStop : function() { 
      clearInterval(this.musicCoverMove);
      this.scale = 0;
      this.coverImage.style.webkitTransform = "rotate(0deg)";
      this.imageBorder.style.webkitTransform = "rotate(0deg)";
    },

    playModeChange : function(){
      if(this.music.playMode==this.playMode.length-1){
        this.music.playMode=0;
        document.querySelector("#playMode").firstChild.setAttribute("xlink:href", "#"+this.playMode[this.music.playMode]);
        return;
      }
      this.music.playMode++;
      document.querySelector("#playMode").firstChild.setAttribute("xlink:href", "#"+this.playMode[this.music.playMode]);
    },

    playMusic : function() {
      if (this.music.paused == true) {
        this.music.play();
        document.querySelector("#play").firstChild.setAttribute("xlink:href", "#icon-weibiaoti--1");
      } else {
        this.music.pause();
        document.querySelector("#play").firstChild.setAttribute("xlink:href", "#icon-weibiaoti--");
      }
    },

    previousMusic : function() {
      if(this.music.playMode==1){
        clearInterval(this.musicBarMove);
        clearInterval(this.musicCoverMove);
        this.music.load();
        this.music.play();
        return;
      }else if(this.music.playMode==2){
        this.musicCoverStop();
        clearInterval(this.musicBarMove);
        this.musicItem = this.musicList[parseInt((Math.random()*(this.musicList.length-1))+1)];
        this.musicName.innerText = this.musicItem.name;
        this.coverImage.style.backgroundImage = "url(\""+ this.musicItem.cover +"\")";
        this.music.setAttribute("src",this.musicItem.src);
        this.music.play();
        return;
      }
      this.musicCoverStop();
      clearInterval(this.musicBarMove);
      this.musicItem = this.musicItem.previous;
      this.musicName.innerText = this.musicItem.name;
      this.coverImage.style.backgroundImage = "url(\""+ this.musicItem.cover +"\")";
      this.music.setAttribute("src",this.musicItem.src);
      this.music.play();
    },

    nextMusic : function() {
      if(this.music.playMode==1){
        clearInterval(this.musicBarMove);
        clearInterval(this.musicCoverMove);
        this.music.load();
        this.music.play();
        return;
      }else if(this.music.playMode==2){
        this.musicCoverStop();
        clearInterval(this.musicBarMove);
        this.musicItem = this.musicList[parseInt((Math.random()*(this.musicList.length-1)))];
        this.musicName.innerText = this.musicItem.name;
        this.coverImage.style.backgroundImage = "url(\""+ this.musicItem.cover +"\")";
        this.music.setAttribute("src",this.musicItem.src);
        this.music.play();
        return;
      }
      this.musicCoverStop();
      clearInterval(this.musicBarMove);
      this.musicItem = this.musicItem.next;
      this.musicName.innerText = this.musicItem.name;
      this.coverImage.style.backgroundImage = "url(\""+ this.musicItem.cover +"\")";
      this.music.setAttribute("src",this.musicItem.src);
      this.music.play();
    },

    musicBarValueChange : function() {
      clearInterval(this.musicBarMove);
      let barLeftWidth = this.musicBar.offsetLeft;
      if (event.clientX - barLeftWidth < 0) {
        this.musicBarValue.style.width = "0px";
        return 0;
      }else if(event.clientX > (barLeftWidth+this.musicBar.offsetWidth)){
        this.musicBarValue.style.width = this.musicBar.offsetWidth + "px";
        return this.musicBar.offsetWidth;
      }else {
        this.musicBarValue.style.width = event.clientX - barLeftWidth + "px";
        return event.clientX - barLeftWidth;
      }
    },

    musicBarChange : function() {
      document.addEventListener("mousemove", this.musicBarValueChange);
      document.addEventListener("mouseup", this.temp);
    },

    temp : function(){
      document.removeEventListener("mousemove", this.musicBarValueChange);
      this.music.currentTime = this.music.duration * (this.musicBarValueChange() / this.musicBar.offsetWidth);
      if(this.music.paused != true) {
        this.musicBarMove;
      }
      document.removeEventListener("mouseup", this.temp);
    }
  }
};

</script>
<style>
.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
#musicPlayer {
  height: 100%;
  width: 100%;
}
#musicCover {
  width: 120px;
  height: 120px;
  margin: 0 auto;
}
#coverImage {
  display: inline-block;
  background: no-repeat 50% 50%;
  background-image: url("http://p2.music.126.net/d541jx7SqoDzVkenhmtg6g==/19193074974743620.jpg?param=500y500");
  background-size: cover;
  height: 80px;
  width: 80px;
  margin: 20px;
}
#imageBorder {
  position: absolute;
  background: url("https://s2.music.126.net/style/web2/img/ie6/singlecover.png") no-repeat 50% 50%;
  background-size: 100%;
  display: inline-block;
  width: 120px;
  height: 120px;
  z-index: 1;
}

#musicController {
  height: 90px;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

#musicList {
  width: 100%;
  height: calc(100% - 210px);
  overflow-y: scroll;
  overflow-x: hidden;
}

#musicList div {
  font-size: 1em;
  font-weight: 700;
  line-height: 2em;
  height: 2em;
  padding: 0 5px;
  overflow: hidden;
  cursor: pointer;
  user-select: none;
  -webkit-user-seletct: none;
  -moz-user-seletct: none;
  -ms-user-seletct: none;
  transition: background-color 0.5s;
}

#musicList div:hover {
  background-color: rgba(90, 90, 90, 0.568);
}

#musicList::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}

#musicList::-webkit-scrollbar-thumb {
  border-radius: 0px;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}

#musicList::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  border-radius: 0px;
  background: rgba(0, 0, 0, 0.1);
}
</style>


